@charset "UTF-8";

@import "import/lib-fonts";
@import "base";
@import "import/typography";
@import "import/reset-css";

/* clearfix */
.clearfix{
	*zoom:1;
	&:before, &:after{
		content:'';
		display:block;
	}
	&:after{
		clear:both;
	}
}

/* hidden */
.hidden {
    display: none;
    overflow: hidden;
    border: 0;
    position: relative;
    z-index: -1;
    width: 1px;
    height: 1px;
    clip: rect(1px, 1px, 1px, 1px);
    clip-path: inset(50%);
}

body.popView{
    overflow: hidden;
    height: 100vh;
}

img {
    width: 100%;
}

input[type="file"]{
    display: none;
}

input::placeholder, textarea::placeholder{
    font-size: inherit;
    color: #aaa;
}

.f-right{float: right;}
.f-left{float: left;}

.po-r{position: relative;}
.po-a{position: absolute;}

.ov-h{overflow: hidden;}

.ta-center{text-align: center !important;}
.ta-left{text-align: left !important;}

.line_01{overflow: hidden; text-overflow: ellipsis; white-space: nowrap;}

.pc_only{display: block;}
.mo_only{display: none;}

/* space */
$position: (t:top, b:bottom, r:right, l: left);
$total: 10;

@each $abbr, $pos in $position {
  @for $i from 0 to $total {
    .m#{$abbr}-#{$i} {margin-#{$pos}: $i*10px !important;}
    .p#{$abbr}-#{$i} {padding-#{$pos}: $i*10px !important;}
  }
}

@media all and (max-width:720px){
  .pc_only{display: none;}
  .mo_only{display: block;}

    /* space */
    $position: (t:top, b:bottom, r:right, l: left);
    $total: 10;

    @each $abbr, $pos in $position {
    @for $i from 0 to $total {
        .m#{$abbr}-#{$i} {margin-#{$pos}: $i*1.39vw !important;}
        .p#{$abbr}-#{$i} {padding-#{$pos}: $i*1.39vw !important;}
    }
    }

}

.contents{overflow: hidden;}
//motion
.motion{opacity: 0; transition: opacity 1s ease-in-out;}
.motion.bottom{transform: translateY(10%); transition: all 1s ease-in-out;}
.motion.left{transform: translateX(-10%); transition: all 1s ease-in-out;}
.motion.right{transform: translateX(10%); transition: all 1s ease-in-out;}

.motion.onTrans{opacity: 1;}
.motion.onTrans.bottom{transform: translateY(0);}
.motion.onTrans.left{transform: translateX(0);}
.motion.onTrans.right{transform: translateX(0);}

.service_con{
  .swiper-slide .txt_con.motion .tag{opacity: 0; transform: translateY(15%); transition: all 1s ease-in-out;}
  .swiper-slide .txt_con.motion h3{opacity: 0; transform: translateY(15%); transition: all 1s ease-in-out; transition-delay: 0.1s;}
  .swiper-slide .txt_con.motion .desc{opacity: 0; transform: translateY(15%); transition: all 1s ease-in-out; transition-delay: 0.2s;}
  .swiper-slide .txt_con.motion .box{opacity: 0; transform: translateY(15%); transition: all 1s ease-in-out; transition-delay: 0.3s;}
  .swiper-slide .img_con.motion{opacity: 0; transform: translateY(2%); transition: all 1s ease-in-out;}

  .swiper-slide.swiper-slide-active .txt_con.motion.onTrans .tag{opacity: 1; transform: translateY(0);}
  .swiper-slide.swiper-slide-active .txt_con.motion.onTrans h3{opacity: 1; transform: translateY(0);}
  .swiper-slide.swiper-slide-active .txt_con.motion.onTrans .desc{opacity: 1; transform: translateY(0);}
  .swiper-slide.swiper-slide-active .txt_con.motion.onTrans .box{opacity: 1; transform: translateY(0);}
  .swiper-slide.swiper-slide-active .img_con.motion.onTrans{opacity: 1; transform: translateY(0);}
}

.resort_con{
  .swiper-slide .txt_con.motion h3{opacity: 0; transform: translateY(15%); transition: all 1s ease-in-out;}
  .swiper-slide .txt_con.motion .tit{opacity: 0; transform: translateY(15%); transition: all 1s ease-in-out; transition-delay: 0.1s;}
  .swiper-slide .txt_con.motion .desc{opacity: 0; transform: translateY(15%); transition: all 1s ease-in-out; transition-delay: 0.2s;}
  .swiper-slide .img_con.motion{opacity: 0; transform: translateY(2%); transition: all 1s ease-in-out;}

  .swiper-slide.swiper-slide-active .txt_con.motion.onTrans h3{opacity: 1; transform: translateY(0);}
  .swiper-slide.swiper-slide-active .txt_con.motion.onTrans .tit{opacity: 1; transform: translateY(0);}
  .swiper-slide.swiper-slide-active .txt_con.motion.onTrans .desc{opacity: 1; transform: translateY(0);}
  .swiper-slide.swiper-slide-active .img_con.motion.onTrans{opacity: 1; transform: translateY(0);}
}

.premium_con{
  .swiper-slide .txt_con.motion h3{opacity: 0; transform: translateY(15%); transition: all 1s ease-in-out;}
  .swiper-slide .txt_con.motion .desc{opacity: 0; transform: translateY(15%); transition: all 1s ease-in-out; transition-delay: 0.1s;}
  .swiper-slide .txt_con.motion .thumb_con{opacity: 0; transform: translateY(15%); transition: all 1s ease-in-out; transition-delay: 0.2s;}
  .swiper-slide .img_con.motion{opacity: 0; transform: translateY(2%); transition: all 1s ease-in-out;}

  .swiper-slide.swiper-slide-active .txt_con.motion.onTrans h3{opacity: 1; transform: translateY(0);}
  .swiper-slide.swiper-slide-active .txt_con.motion.onTrans .desc{opacity: 1; transform: translateY(0);}
  .swiper-slide.swiper-slide-active .txt_con.motion.onTrans .thumb_con{opacity: 1; transform: translateY(0);}
  .swiper-slide.swiper-slide-active .img_con.motion.onTrans{opacity: 1; transform: translateY(0);}
}

@media all and (max-width:1200px){
  .premium_sec .premium_con .swiper-slide .txt_con.motion .thumb_con.type2{transform: translate(-65px, 15%);}
  .premium_sec .premium_con .swiper-slide.swiper-slide-active .txt_con.motion.onTrans .thumb_con.type2{transform: translate(-65px, 0);}
}

@media all and (max-width:960px){
  .premium_sec .premium_con .swiper-slide .txt_con.motion .thumb_con.type2{transform: translate(0, 15%);}
  .premium_sec .premium_con .swiper-slide.swiper-slide-active .txt_con.motion.onTrans .thumb_con.type2{transform: translate(0, 0);}
}